<template>
    <div class="ex1">
        <div class="app-sec">
            <div class="app-col">
                <h2>Example 1: With Transform</h2>
                <p>Basic horizontal scroll with <code>transform</code> property</p>
                <pre data-highlight>
new ScrollBooster({
  viewport: document.querySelector('.example1-viewport'),
  content: document.querySelector('.example1-content'),
  scrollMode: 'transform', // use CSS 'transform' property
  direction: 'horizontal', // allow only horizontal scrolling
  emulateScroll: true, // scroll on wheel events
});</pre>
            </div>
            <div class="app-col">
                <app-table viewport-class="example1-viewport" content-class="example1-content"></app-table>
            </div>
        </div>
    </div>
</template>

<script>
    import ScrollBooster from 'scrollbooster'
    import AppTable from './Table.vue'

    export default {
        name: "app-example1",
        components: { AppTable },
        mounted() {
            initScroll()
        }
    }

    function initScroll() {
        new ScrollBooster({
            viewport: document.querySelector('.example1-viewport'),
            content: document.querySelector('.example1-content'),
            scrollMode: 'transform',
            direction: 'horizontal',
            emulateScroll: true,
        })
    }

</script>

