<template>
    <div class="ex2">
        <div class="app-sec">
            <div class="app-col">
                <h2>Example 2: Sync With Native Scroll</h2>
                <p>Content scroll in horizontal dimension with <code>scrollLeft</code> property</p>
                <pre data-highlight>
new ScrollBooster({
  viewport: document.querySelector('.example2-viewport'),
  content: document.querySelector('.example2-content'),
  scrollMode: 'native',
  direction: 'horizontal'
});</pre>
            </div>
            <div class="app-col">
                <app-table viewport-class="example2-viewport" content-class="example2-content"></app-table>
            </div>
        </div>
    </div>
</template>

<script>
    import ScrollBooster from 'scrollbooster'
    import AppTable from './Table.vue'

    export default {
        name: "app-example2",
        components: { AppTable },
        data() {
            return {}
        },
        mounted() {
            initScroll()
        }
    }

    function initScroll() {
        new ScrollBooster({
            viewport: document.querySelector('.example2-viewport'),
            content: document.querySelector('.example2-content'),
            scrollMode: 'native',
            direction: 'horizontal',
        })
    }

</script>

<style lang="less">
    .ex2 .tbl {
        background: #fff;
        &-main {
            overflow-x: scroll;
            -webkit-overflow-scrolling: touch;
        }
    }
</style>