<template>
    <div class="tbl">
        <div class="tbl-left">
            <table class="table">
                <thead class="thead">
                <tr>
                    <th>#</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(row, ind) in rows" v-bind:key="ind">
                    <td>{{ind + 1}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <div class="tbl-main-wrap">
            <div v-bind:class="['tbl-main', viewportClass]">
                <table v-bind:class="['table', contentClass]">
                    <thead class="thead">
                    <tr>
                        <th v-for="(col, ind) in rows[0].cols" v-bind:key="ind">
                            {{col.caption}}
                        </th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(row, ind) in rows" v-bind:key="ind">
                        <td v-for="(col, ind) in row.cols" v-bind:key="ind">
                            {{col.val}}
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <div class="tbl-right">
            <table class="table">
                <thead class="thead">
                <tr>
                    <th>#</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(row, ind) in rows" v-bind:key="ind">
                    <td><input type="checkbox"></td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    let generateData = () => {
        let rows = []
        let words = ["first", "UI", "scroll", "drag", "dude", "boost", "bounce", "wow"]
        for (let i = 0; i < 8; i++) {
            let cols = []
            for (let j = 0; j < 15; j++) {
                cols.push({
                    caption: words[Math.round((words.length - 1) * Math.random())],
                    val: words[Math.round((words.length - 1) * Math.random())] + ' ' + words[Math.round((words.length - 1) * Math.random())]
                })
            }
            rows.push({ cols })
        }
        return rows
    }

    let data = generateData()

    export default {
        name: "app-table",
        props: ['viewportClass', 'contentClass'],
        data() {
            return {
                rows: data
            }
        }
    }
</script>

