<template>
    <div class="ex3">
        <div class="app-sec">
            <div class="app-col">
                <h2>Example 3: Vertical And Horizontal Scroll</h2>
                <p>Both horizontal and vertical scroll are supported, with optional mouse wheel emulation</p>
                <pre data-highlight>
const viewport = document.querySelector('.example3-viewport');
const content = document.querySelector('.example3-content');
const image = document.querySelector('.example3-image');

const sb = new ScrollBooster({
  viewport,
  content,
  scrollMode: 'transform',
  emulateScroll: true
});

image.addEventListener('load', () => {
  // set viewport position to the center of an image
  const offsetX = image.scrollWidth - viewport.offsetWidth;
  const offsetY = image.scrollHeight - viewport.offsetHeight;
  sb.setPosition({
    x: offsetX / 2,
    y: offsetY / 2
  });
});</pre>
            </div>
            <div class="app-col">
                <div class="example3-viewport">
                    <div class="example3-content">
                        <img src="./assets/pexels-photo-590478.jpg" alt="" class="example3-image">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    // import img from './assets/pexels-photo-940365.jpg'
    import ScrollBooster from 'scrollbooster'

    export default {
        name: "app-example3",
        data() {
            return {}
        },
        mounted() {
            initScroll()
        }
    }

    function initScroll() {
        const viewport = document.querySelector('.example3-viewport')
        const content = document.querySelector('.example3-content')
        const image = document.querySelector('.example3-image')

        const sb = new ScrollBooster({
            viewport,
            content,
            scrollMode: 'transform',
            emulateScroll: true
        })

        image.addEventListener('load', () => {
            const offsetX = image.scrollWidth - viewport.offsetWidth
            const offsetY = image.scrollHeight - viewport.offsetHeight
            sb.setPosition({
                x: offsetX / 2,
                y: offsetY / 2
            })
        })
    }

</script>

<style lang="less">
    .example3-viewport {
        // max-width: 500px;
        height: 340px;
        overflow: hidden;
        margin: auto;
        border: 3px solid #ffab92;
        background: #ffab92;
        border-radius: 6px;
        box-shadow: 0 2px 10px rgba(0,0,0,.1);
        cursor: move;
    }

    .example3-image {
        display: block;
    }
</style>