<template>
    <div class="header">
        <div class="header-logo">
            <div class="header-logo-abbr">SB</div>
            <canvas id="canvas"></canvas>
        </div>
        <h1 class="header-caption">
            <a class="header-link" href="https://github.com/ilyashubin/scrollbooster">
                ScrollBooster
            </a>
        </h1>
        <h2 class="header-sub">Enjoyable content drag-to-scroll library</h2>
    </div>
</template>

<script>
    export default {
        name: "app-header",
        mounted() {
            makeLogo()
        }
    }

    function makeLogo() {
        let wrapper = document.querySelector('.header-logo')
        let canvas = document.querySelector('#canvas')
        let c = canvas.getContext('2d')

        let width = canvas.width = wrapper.offsetWidth
        let height = canvas.height = wrapper.offsetHeight

        let offset = 10

        function roundToNearest (val, nearest) {
            return Math.round(val / nearest) * nearest
        }

        class Particle {
            constructor() {
                this.x = Math.random() * width
                this.y = this.getY()
                this.w = 20 + Math.round(Math.random() * 50)
                this.h = 3
            }

            getY() {
                return roundToNearest(offset + (Math.random() * (height - offset)), Math.round(height / 6))
            }

            move () {
                this.x -= 2
                if (this.x + this.w < 0) {
                    this.x = width + Math.round(Math.random() * 150)
                    this.y = this.getY()
                }
            }
        }

        let particles = []
        particles = Array.apply(null, Array(15)).map(()=> {
            return new Particle()
        })

        let animate = () => {
            c.clearRect(0,0,width,height)
            c.fillStyle = '#fff'
            c.fillStyle = '#1cb4dc'

            particles.forEach(p => {
                c.fillRect(p.x, p.y, p.w, p.h)
                p.move()
            })

            requestAnimationFrame(animate)
        }
        animate()
    }
</script>

